    /* font-family: "Radio Canada", sans-serif; */
    height: 100%;
    /* color: white; */
}

hr {
    height: 10px;
    width: 100%;
    background-color: white;
    border: 0 none;
}
/* Source for centering: https://stackoverflow.com/questions/9862167/positioning-div-element-at-center-of-screen */
#menu, #ending {
    background-color: black;
    background-size: cover;
    background-position-y: center;
    height: 100%;
}

#space {
    background-color: transparent;
    background-size: cover;
    background-position-y: center;
    height: 100%;
}
#space-end {
    position: fixed;
    top: 25%;
    left: 50%;
    text-align: center;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
#message {
    background: #f58025;
    color: white;
    font-size: 50px;
    padding: 35px;
    border-radius: 0px;
    text-align: center;
    font: sans-serif;
}

#start {
    position: fixed;
    top: 50%;
    left: 75%;
    /* text-align: center; */
    /* -webkit-transform: translate(-50%, -50%); */
    transform: translate(-50%, -50%);
}

#end, #pauseText {
    position: fixed;
    top: 0%;
    left: 50%;
    text-align: center;
    /* -webkit-transform: translate(-50%, -50%); */
    transform: translate(-50%, -50%);
}


#title, #finalScore {
    color: white;
    font-size: 4rem;
}
/* Inspired by https://cssdeck.com/blog/how-to-create-a-sticky-footer-using-css/ */
#footer{
    position: fixed;
    left: 0px;
    bottom: 0px;
    height: 50px;
    width: 100%;
    color: white;
    font-size: 0.75rem;
    padding-left: 50px;
    z-index: 2;
}

/* a:-webkit-any-link {
    color: white;
    cursor: pointer;
    text-decoration: underline;
} */

#score{
    font-size: 2rem;
}

#reminders{
    position: absolute;
    top: 0px;
    height: 50px;
    width: 100%;
    margin-right: 0;
    margin-left: 20px;
    margin-top: 20px;
    color: white;
}

#instructionsText {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    text-align: center;
    font-size: 20px;
    width: 65%
    /* position: fixed;
    margin: auto;
    text-align: center;
    color: white; */
}

#gameover {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: white;
    font-size: 20px;
}

#instructions {
    /* font-size: 0.75rem; */
    background: rgba(0, 0, 0, 0.75);
    /* background-color: black;
    background-size: cover;
    background-position-y: center;
    height: 100%; */
}

#fillScreen, #instructions{
    position:fixed;
    padding:0;
    margin:0;

    top:0;
    left:0;

    width: 100%;
    height: 100%;
    z-index: 1;
}

#pause, #main, #over {
    background: rgba(0, 0, 0, 0.75)
}

.invisible {
    visibility: hidden;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,6CAA6C;IAC7C,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,uBAAuB;IACvB,cAAc;AAClB;AACA,kHAAkH;AAClH;IACI,uBAAuB;IACvB,sBAAsB;IACtB,6BAA6B;IAC7B,YAAY;AAChB;;AAEA;IACI,6BAA6B;IAC7B,sBAAsB;IACtB,6BAA6B;IAC7B,YAAY;AAChB;AACA;IACI,eAAe;IACf,QAAQ;IACR,SAAS;IACT,kBAAkB;IAClB,wCAAwC;IACxC,gCAAgC;AACpC;AACA;IACI,mBAAmB;IACnB,YAAY;IACZ,eAAe;IACf,aAAa;IACb,kBAAkB;IAClB,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,eAAe;IACf,QAAQ;IACR,SAAS;IACT,wBAAwB;IACxB,8CAA8C;IAC9C,gCAAgC;AACpC;;AAEA;IACI,eAAe;IACf,OAAO;IACP,SAAS;IACT,kBAAkB;IAClB,8CAA8C;IAC9C,gCAAgC;AACpC;;;AAGA;IACI,YAAY;IACZ,eAAe;AACnB;AACA,kFAAkF;AAClF;IACI,eAAe;IACf,SAAS;IACT,WAAW;IACX,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,kBAAkB;IAClB,UAAU;AACd;;AAEA;;;;GAIG;;AAEH;IACI,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,YAAY;IACZ,WAAW;IACX,eAAe;IACf,iBAAiB;IACjB,gBAAgB;IAChB,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,SAAS;IACT;;;mBAGe;AACnB;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,kBAAkB;IAClB,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,wBAAwB;IACxB,+BAA+B;IAC/B;;;mBAGe;AACnB;;AAEA;IACI,cAAc;IACd,SAAS;IACT,QAAQ;;IAER,KAAK;IACL,MAAM;;IAEN,WAAW;IACX,YAAY;IACZ,UAAU;AACd;;AAEA;IACI;AACJ;;AAEA;IACI,kBAAkB;AACtB","sourcesContent":["body, html {\n    /* font-family: \"Radio Canada\", sans-serif; */\n    height: 100%;\n    /* color: white; */\n}\n\nhr {\n    height: 10px;\n    width: 100%;\n    background-color: white;\n    border: 0 none;\n}\n/* Source for centering: https://stackoverflow.com/questions/9862167/positioning-div-element-at-center-of-screen */\n#menu, #ending {\n    background-color: black;\n    background-size: cover;\n    background-position-y: center;\n    height: 100%;\n}\n\n#space {\n    background-color: transparent;\n    background-size: cover;\n    background-position-y: center;\n    height: 100%;\n}\n#space-end {\n    position: fixed;\n    top: 25%;\n